<template>
  <div v-if="renderComponent" class="search_scroll">
    <div class="search-bar">
      <input @input="search" v-model="value" type="text" placeholder="Поиск..." />
    </div>

    <div class="users" v-if="users">
      <div class="user_item" v-for="item in users" :key="item.id">
        <img v-if="item.profile_picture" :src="'https://diplom-api.na4u.ru/uploads/' + item.profile_picture.split('/').pop()" alt="">
        <img v-else src="https://diplom-api.na4u.ru/uploads/auto.jpg" alt="">
        <div class="right">
          <h1 class="title">
            {{ item.login }}
          </h1>
          <h3>{{ item.phone }}</h3>
        </div>
        <button @click="createChat(item.id)">+</button>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        value: '',
        users: [],
        chatIds: [],
        renderComponent: true
      }
    },
    beforeCreate() {

        var headers = new Headers();
        headers.append("Authorization", localStorage.getItem('token'));
        fetch(`https://diplom-api.na4u.ru/api/chat/get`, {method: 'GET', headers: headers})
            .then(response => response.json())
            .then(result => {
                if (!result.values.message) {
                    result.values.forEach(item => {
                        this.chatIds.push(item.interlocutor.id)
                    });
                } else this.chatIds = []
            })
            .catch(error => console.log('error', error));
    },
    methods: {
        search() {
            if (!this.value)
                this.users = [];

            this.users = [];

            var headers = new Headers();
            headers.append("Authorization", localStorage.getItem('token'));

            var requestOptions = {
                method: 'GET',
                headers: headers,
                redirect: 'follow'
            };

            fetch(`https://diplom-api.na4u.ru/api/user/search?q=${this.value}`, requestOptions)
                .then(response => response.json())
                .then(result => {
                    if (result.values) {
                        let tmpResult = [];   
                        result.values.forEach(item => {
                            if (item.id != localStorage.getItem('uid') && !this.chatIds.includes(item.id))
                                tmpResult.push(item);
                        });
                        if (tmpResult) this.users = tmpResult;
                    }  
                })
                .catch(error => console.log('error', error));
        },
        createChat(id) {
            var headers = new Headers();
            headers.append("Authorization", localStorage.getItem('token'));
            headers.append("Content-Type", "application/json");

            var raw = JSON.stringify({
                "id": id
            });

            var requestOptions = {
                method: 'POST',
                headers: headers,
                body: raw,
                redirect: 'follow'
            };

            fetch("https://diplom-api.na4u.ru/api/chat/create", requestOptions)
            .then(response => response.json())
            .then(result => {
                console.log(result)
                if(result.values.insertId) {
                  localStorage.setItem('chat_id', result.values.insertId)
                } else {
                  localStorage.setItem('chat_id', result)
                }
                this.$router.push({ path: "/" });
            })
            .catch(error => console.log('error', error));
        }
    }
  }
</script>


